body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(./background.png); 
  background-repeat: no-repeat;
  background-size: cover;
}

.App{
  background: #4c2a4c;
  margin: 30px auto 100px auto;
  width: 90%;
  max-width: 700px;
  color: #eee;
  border-radius: 10px;


}
.navbar{
  padding: 10px 20px;
  text-align: center;
  background: #6d3d6d;
  border-radius: 10px;
}
.navbar h1{
  margin: 10px 0;
}

.todo-list{
  margin: 20px;
}
.todo-list ul{
  padding: 0;
  list-style-type: none;
}
.todo-list li{
  background: #6d3d6d;
  border-radius: 4px;
  padding: 10px;
  cursor: pointer;
  margin: 10px 0;
  border-radius: 10px;
}
.todo-list li:hover{
  opacity: 0.7;
  text-decoration: line-through;
}
.todo-list .title{
  font-weight: bold;
  color: #fff;
  font-size: 1.2em;
}
.todo-list .brief{
  font-size: 0.9em;
  color: #ddd;
}
.empty{
  margin: 20px;
  text-align: center;
}

form{
  padding: 20px;
}
input[type='text']{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  background: #3c1f3c;
  color: #fff;
  border: 0;
  border-radius: 10px;
}
input[type='submit']{
  margin: 10px auto;
  background: #eee;
  border: 0;
  padding: 6px 20px;
  display: block;
  border-radius: 25px;
}

footer {
  background-color: white;
  height: 50px;
  margin-top: 22%;
}

footer {
  position: absolute;
  bottom: 0;
  display: inline-block;
  width: 100%;
  position: fixed;
  padding-top: 2px;  
}
footer p {
  margin-left: 43%;
}
#credit a {
  text-decoration: none;

}

@media screen and (max-width: 425px){
  html{height: 100%;}
  body {
  background-image: url(./bg-mobile.png);
  background-size: cover;
  background-repeat: repeat;
    
  }
  #credit {margin-left: 20%;}
}